import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { SelectItem, Message, ConfirmDialogModule, ConfirmationService } from 'primeng/primeng';
import { CusOperationHttpService } from './../../../http/custom-operation/custom-operation.http.service';
import * as _ from 'lodash';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
    selector: 'save-customGroup-admin',
    templateUrl: './save-customGroup-admin.component.html',
    styleUrls: ['./save-customGroup-admin.component.scss'],
    providers: [CusOperationHttpService,Commfunc],
})
export class SaveCustomGroupAdminComponent implements OnInit {
    @Output() event: EventEmitter<any> = new EventEmitter<any>();
    @Input() isContactPerson: any;
    @Input() inValue: any;
    @Input() adminFlag: any;

    //公共
    tableMesg: any = tableMessage;
    // 中间量
    outValue: any = [];
    // 第二层模态框
    addDisplay: boolean = false;
    staff_display: boolean = false;//人员模态框
    staff_info: any;
    // 中间量
    groupZone: any = '';
    Property: boolean = false;
    // 假数据
    staff: any = '';
    staffArr: any = [
        { label: '请选择', value: ['', "请选择"] },
        { label: '人员临时1', value: ['1', "人员临时1"] },
        { label: '人员临时2', value: ['2', "人员临时2"] },
        { label: '人员临时3', value: ['3', "人员临时3"] },
        { label: '人员临时4', value: ['4', "人员临时4"] },
        { label: '人员临时5', value: ['5', "人员临时5"] },
        { label: '人员临时6', value: ['6', "人员临时6"] },
        { label: '人员临时7', value: ['7', "人员临时7"] },
        { label: '人员临时8', value: ['8', "人员临时8"] },
        { label: '人员临时9', value: ['9', "人员临时9"] },
    ];
    userIdTemp: any = this.staffArr[0].value[0];
    userNameTemp: any = this.staffArr[0].value[1];
    data: any;//公共接口返回人员

    // 控件
    msgs: Message[] = [];
    // 模糊自动补全查询框
    filteredCountriesSingle: any[];
    filteredCountriesMultiple: any[];
    brands: string[] = ['Audi', 'BMW', 'Fiat', 'Ford', 'Honda', 'Jaguar', 'Mercedes', 'Renault', 'Volvo', 'VW'];
    filteredBrands: any[];
    brand: string;
    /**
     * 2018.1.9--
     */
    // 取session当前客户
    ownerId: any = '';
    ownerName: any = '';
    // 标志
    hasFlag: any = 0;//重复标志
    concatFlag: any = 0;//联系人禁止标志
    // invalue暂存
    oldValue: any = [];
    /**
     *--2018.1.9
     */

    constructor(
        public httpService: CusOperationHttpService,
        public router: Router,
        public routerInfo: ActivatedRoute,
        public commfunc: Commfunc,
    ) { }
    ngOnInit() {
        this.preSet();
    }
    preSet() {
        let session = window.sessionStorage;
        let data = session.getItem('commonHeader');
        if (!data) {
            this.router.navigate(["/login"]);
            return;
        }
        let mesg = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (!mesg) {
        }
        let ch = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        this.ownerId = mesg['userId']//群主id
        this.ownerName = ch['userName'];//群主名
    }
    delArray(i) {
        // 删除行
        this.inValue.splice(i, 1);
        this.inValue = _.clone(this.inValue);
    }
    return() {
        // 返回

        this.inValue = _.clone(this.inValue);
        this.event.emit({
            adminDisplay: false,
            outValue: this.inValue,
            adminFlag: this.adminFlag,
        })
    }
    // 第二层模态框
    changeStaff(event) {
        // 下拉框选择

        // 正式需修改
        this.userIdTemp = event.value[0];
        this.userNameTemp = event.value[1];
    }
    addBtn() {
        // 确定返回

        // 基本校验
        let tmpFlag1 = true;
        tmpFlag1 = this.isDisable();
        if (!tmpFlag1) {
            return;
        }

        // 是否重复
        let tmpFlag3 = true;
        tmpFlag3 = this.isHave();
        if (!tmpFlag3) {
            this.addDisplay = false;
            return;
        }

        // 需修改
        this.inValue.push(
            {
                groupZone: this.groupZone,
                userId: this.userIdTemp,
                userName: this.userNameTemp,
                statusId: '',
            }
        )
        this.inValue = _.clone(this.inValue);
        // 重置下拉框
        this.staff = '';
        this.groupZone = '';
        this.userIdTemp = this.staffArr[0].value[0];
        this.userNameTemp = this.staffArr[0].value[1];
        // 模态框
        this.addDisplay = false;
    }

    isContact() {
        // 是否是联系人
        if (!this.isContactPerson) {
            if (this.inValue.length >= 1) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '非联系人不可选择多条,请删除当前条或不做更改!' }];
                return false;
            }
        }
        return true;
    }
    isHave() {
        // 是否重复
        for (let i in this.inValue) {
            if (this.inValue[i].userId == this.userIdTemp && this.inValue[i].userName == this.userNameTemp) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '不能重复添加!' }];
                return false;
            }
        }
        return true;
    }
    isDisable() {
        // 基本校验

        if (!this.userIdTemp || this.userIdTemp == '') {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '角色名称不能为空!' }];
            return false;
        }
        if (this.isContactPerson && (!this.groupZone || this.groupZone == '')) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '客群区域不能为空!' }];
            return false;
        }
        return true;
    }

    // 模糊查询
    filterBrands(event) {
        this.filteredBrands = [];
        for (let i = 0; i < this.brands.length; i++) {
            let brand = this.brands[i];
            if (brand.toLowerCase().indexOf(event.query.toLowerCase()) == 0) {
                this.filteredBrands.push(brand);
            }
        }
    }
    returnInfo(data) {
        /**
         * 2018.1.9以下
         */
        if (data.length > 1) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '每次只能添加一人!' }];
            this.hasFlag = 1;
            return;
        }
        else {
            this.hasFlag = 0;
        }
        if (this.isContactPerson && data[0].tellerId === this.ownerId && data[0].tellerName === this.ownerName) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '不能添加当前用户为联系人!' }];
            this.concatFlag = 1;
            return;
        }
        else {
            this.concatFlag = 0;
        }
        /**
         * 2018.1.9以上
         */
        this.data = data;
        this.staff_info = data[0].tellerName;
        this.staff = [data[0].tellerId, data[0].tellerName];
        this.staff_display = false;

        this.addBtn2();//2018.1.9
    }
    staffClick() {
        this.staff_display = true;
    }
    // 第一层模态框
    showDialog() {
        /**
        * 2018.1.9以下
        */
        this.oldValue = _.clone(this.inValue);
        /**
         * 2018.1.9以上
         */
        this.Property = true;
        if (this.adminFlag == 1) {
            // 增加行
            // 是否是联系人
            let tmpFlag2 = true;
            tmpFlag2 = this.isContact();
            if (!tmpFlag2) {
                this.addDisplay = false;
                return;
            }
            // 重置下拉框        
            this.staff = '';
            this.groupZone = '';
            this.userIdTemp = this.staffArr[0].value[0];
            this.userNameTemp = this.staffArr[0].value[1];
            // 模态框
            this.addDisplay = true;
        } else {
            this.staffClick()
        }
    }
    addBtn2() {
        /**
         * 2018.1.9以下
        */
        this.staff_info = '';
        if (!this.staff || this.staff.length === 0) {
            this.addDisplay = false;
            return;
        }
        /**
         * 2018.1.9以上
         */
        if (this.adminFlag == 1 || this.adminFlag == 5) {

        }
        if (this.adminFlag == 1) {
            if (this.inValue.length > 5) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '您好，群联系人最多只能为六个' }];
            } else {
                this.inValue.push({
                    userId: this.staff[0],
                    userName: this.staff[1],
                    groupZone: this.groupZone
                })
                this.inValue = _.clone(this.inValue)
            }

        } else if (this.adminFlag != 5 && this.adminFlag != 3) {
            let stu = this.inValue.every(item => {
                this.data.forEach(item_2 => {
                    return item_2.userId != item.userId
                });

            })
            if (stu) {
                this.data.forEach(item_3 => {
                    this.inValue.push({
                        userId: item_3.tellerId,
                        userName: item_3.tellerName,
                        groupZone: this.groupZone
                    })
                });
                this.inValue = _.clone(this.inValue)
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '您好，人员不能重复' }];
            }

        } else {
            if (this.inValue.length > 0) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '您好，只能添加一个' }];
            } else {
                let stu = this.inValue.every(item => {
                    return this.staff[0] != item.userId
                })
                if (stu) {
                    this.inValue.push({
                        userId: this.staff[0],
                        userName: this.staff[1],
                    })
                    this.inValue = _.clone(this.inValue)
                } else {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '您好，人员不能重复' }];
                }
            }
        }

        this.addDisplay = false;
    }
    returnInfoHide() {
        /**
         * 2018.1.9以下
        */
        // this.addBtn2();
        /**
         * 2018.1.9以上
         */
        this.Property = false;
    }
}